@import "src/shared/styles/common.scss";
.outer {
  position: fixed;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 56px;
  top: 0;
  left: 0;
  z-index: -1;
  display: flex;
  backdrop-filter: blur(16px);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.15s ease-in;
  overflow-y: auto;

  &_visible {
    z-index: 1;
    opacity: 1;
    pointer-events: auto;
  }
}

.inner {
  position: relative;
  margin: auto;
  width: min(90vw, 460px);
  padding: 48px 56px;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 1.6;
  background: var(--color-main-inverted);
  overflow: auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-main);
  opacity: 0.8;
}

.close {
  position: absolute;
  top: 8px;
  right: 8px;
  fill: var(--color-main-inverted);
}
